.order-item{
  background-image: url('../imgs/bg8.png');
  border: 3px solid black;
  height: 100px;
  display: flex;
  align-items: center;
  padding: 10px 10px;
  position: relative;
}

.order-item-img{
  height: 100%;
  border: 3px solid black;
}

.order-item-name-and-amount{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  color: black;
  text-decoration: none;
}

.order-item-price{
  position: absolute;
  bottom: 10px;
  right: 10px;
  text-decoration: underline;
  color: black;
}

@media screen and (max-width: 400px) {
  .checkout h2{
    font-size: 20px;
  }

  .checkout h3{
    font-size: 16px;
  }

  .order-item-name-and-amount{
    position: relative;
  }
}